import React, { useState, useEffect } from 'react';
import { getMediaSource, checkVideoSupport } from '../config/media';

const BackgroundVideo = () => {
  const [isVideoLoaded, setIsVideoLoaded] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [canPlayVideo, setCanPlayVideo] = useState(true);

  useEffect(() => {
    // Verificar si es dispositivo móvil
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768);
    };
    
    // Verificar soporte de video
    setCanPlayVideo(checkVideoSupport());

    // Verificar tamaño inicial
    checkMobile();

    // Escuchar cambios de tamaño
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const mediaSources = getMediaSource('hero', isMobile ? 'mobile' : 'desktop');

  return (
    <>
      {/* Fondo de respaldo y gradiente */}
      <div 
        className={`absolute inset-0 bg-gradient-to-r from-primary to-secondary transition-opacity duration-1000 ${
          isVideoLoaded && canPlayVideo ? 'opacity-0' : 'opacity-100'
        }`}
        style={{
          backgroundImage: `url('${mediaSources.fallback}')`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
        }}
      />
      
      {/* Video de fondo */}
      {canPlayVideo && (
        <video 
          className={`video-background transition-opacity duration-1000 ${
            isVideoLoaded ? 'opacity-100' : 'opacity-0'
          }`}
          autoPlay 
          loop 
          muted 
          playsInline
          poster={mediaSources.poster}
          onLoadedData={() => setIsVideoLoaded(true)}
        >
          <source 
            src={mediaSources.video}
            type="video/mp4" 
          />
        </video>
      )}
      
      {/* Overlay con gradiente */}
      <div className="overlay bg-gradient-to-b from-black/60 via-black/50 to-black/60">
        {/* Patrón de puntos para textura */}
        <div 
          className="absolute inset-0 opacity-10"
          style={{
            backgroundImage: 'radial-gradient(circle, #fff 1px, transparent 1px)',
            backgroundSize: '20px 20px'
          }}
        ></div>
      </div>
    </>
  );
};

export default BackgroundVideo;
